import axios from 'axios'
import qs from 'qs'
import {
  getCookie
} from './utils.js'

const ajaxBaseUrl = 'https://api.yswf.xyz/api/scenic'
// 
axios.interceptors.request.use(function (config) {
  // 
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(function (response) {
  // 
  if (response.data) {
    return response.data
  } else {
    return Promise.reject(response)
  }
}, function (error) {
  // 
  return Promise.reject(error)
})
/**
 *
 * @param {string} url
 * @param {object} data
 * @param {string} type
 * @param {function} callFunc
 * @param {function} filter
 */
export function fetchData (url, data, type, callFunc, filter) {
  let sendData = ''
  if (filter && filter.constructor === Function) {
    sendData = filter(data)
  }
  const config = {}
  config.method = type || 'post'
  config.url = ajaxBaseUrl + url
  config.headers = {
    // 'access-token': getCookie('access-token'),
  }
  if (type === 'get') {
    config.params = data
  } else {
    config.data = sendData ? qs.stringify(data) : qs.stringify(data)
  }
  if (!callFunc) {
    return axios(config)
  }
  axios(config).then(res => {
    callFunc(res)
  }).catch(err => {
    callFunc({
      desc: 'axios',
      err: err
    })
    console.log('axios', err)
  })
}
