import { fetchData } from './ajax'
// 
export function login (data, type, callFunc) {
  var msg = data
  if (callFunc && (callFunc.constructor === Function)) {
    return fetchData('/login', msg, type, callFunc)
  }
  return fetchData('/login', msg, type)
}
